body {
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #282c36;
    color: white;
}

.timer {
    display: flex;
    gap: 15px;
    background: #3a3f4b;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.field {
    text-align: center;
}

.value {
    font-size: 2rem;
    font-weight: bold;
    display: block;
}

.label {
    font-size: 0.9rem;
    color: #bbb;
}